import React, { useState } from "react";

import "./msg.css";
const { addComment } = require("../axios");

const Msg = () => {
  const [inputValue, setInputValue] = useState({
    name: "",
    comment: "",
    pw: "",
  });

  const { name, comment, pw } = inputValue;

  const onChange = (e) => {
    const { className, value } = e.target;
    const nextInputs = {
      ...inputValue,
      [className]: value,
    };
    setInputValue(nextInputs);
  };

  return (
    <div className="msg">
      <h3> </h3>
      <div className="commentBox">
        <form>
          <input
            required
            placeholder=""
            className="name"
            onChange={onChange}
          ></input>
          <input
            required
            placeholder=""
            className="pw"
            onChange={onChange}
            type="password"
            maxLength={4}
          ></input>
          <br></br>
          <textarea
            placeholder=""
            className="comment"
            onChange={onChange}
            rows="2"
            cols="33"
          ></textarea>
          <br></br>
          <button
            type="submit"
            onClick={() => addComment(name, comment, pw).then()}
          >
            
          </button>
        </form>
      </div>
    </div>
  );
};

export default Msg;
